/* ------------------- GLOBAL --------------------- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&family=Outfit:wght@400;500;600;700;800&family=Poppins:wght@400;500;600;700;800&display=swap');

/* Global font settings */
body {
  font-family: 'Inter', 'Outfit', 'Poppins', sans-serif;
  overflow-x: hidden;
}

/* Buttons and emphasized text */
button,
strong {
  font-family: 'Outfit', 'Inter', sans-serif;
  font-weight: 600;
}

html {
  scroll-behavior: smooth;
}

/* Exact gradient matching the screenshot */
body {
  background: linear-gradient(87deg, #3F1D38, #3F1D38, #AF5279, #FFD6A5);
  position: relative;
  min-height: 100vh;
  overflow-x: hidden;
}

.Home {
  width: 100%;
  min-height: 110vh;
  background: transparent;
  padding: 20px 0 0 0;
  position: relative;
  color: #FFD6A5;
  overflow-x: hidden;
  z-index: 1;
}

.home-link {
  display: flex;
  text-decoration: none;
  color: inherit;
  transition: transform 0.3s ease;
}

.home-link:hover {
  transform: scale(1.02);
}

/* ------------------- NAVBAR --------------------- */
.navbar {
  display: flex;
  align-items: center;
  padding: 14px 24px;
  background: rgba(63, 29, 56, 0.4);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border-radius: 16px;
  border: 1px solid rgba(255, 214, 165, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 214, 165, 0.1) inset;
  position: sticky;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
  max-width: 1150px;
  margin: 10px auto auto 50%;
  z-index: 1000;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  animation: navSlideDown 0.6s ease-out;
}

.navbar:hover {
  background: rgba(63, 29, 56, 0.5);
  border-color: rgba(175, 82, 121, 0.4);
  box-shadow: 0 12px 40px rgba(175, 82, 121, 0.3),
    0 0 0 1px rgba(255, 214, 165, 0.2) inset;
}

@keyframes navSlideDown {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.navbar-logo-container {
  display: flex;
  align-items: center;
  transition: transform 0.3s ease;
}

.navbar-logo-container:hover {
  transform: scale(1.05);
}

.navbar-logo {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  margin-right: 10px;
  border: 2px solid rgba(255, 214, 165, 0.3);
  transition: all 0.3s ease;
  box-shadow: 0 0 15px rgba(255, 214, 165, 0.2);
}

.navbar-logo:hover {
  border-color: rgba(175, 82, 121, 0.6);
  box-shadow: 0 0 25px rgba(175, 82, 121, 0.5);
  transform: rotate(360deg);
}

.navbar-title {
  font-size: 22px;
  padding: 3px;
  color: #FFD6A5;
  font-weight: 700;
  letter-spacing: 0.5px;
  background: linear-gradient(135deg, #FFD6A5, #AF5279);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: titleShimmer 3s ease-in-out infinite;
}

@keyframes titleShimmer {

  0%,
  100% {
    filter: brightness(1);
  }

  50% {
    filter: brightness(1.2);
  }
}

.navbar-links {
  display: flex;
  margin-left: auto;
  list-style: none;
  gap: 8px;
}

.link {
  margin-left: 0;
}

.home-link {
  background: transparent;
  border: none;
}

.link a {
  text-decoration: none;
  color: #FFD6A5;
  font-size: 16px;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 10px;
  border-bottom: 2px solid transparent;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  display: inline-block;
}

.link a::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #AF5279, #FFD6A5);
  transform: translateX(-50%);
  transition: width 0.3s ease;
}

.link a:hover::before {
  width: 80%;
}

.link a:hover {
  color: #fff;
  background: rgba(175, 82, 121, 0.15);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(175, 82, 121, 0.3);
}

/* ------------------- HERO SECTION --------------------- */
.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 150px 100px;
  gap: 50px;
  text-align: center;
  position: relative;
  z-index: 2;
  animation: heroFadeIn 1s ease-out;
}

@keyframes heroFadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-text {
  max-width: 600px;
  flex: 1;
  order: 2;
  animation: textSlideIn 0.8s ease-out 0.2s both;
}

@keyframes textSlideIn {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.hero-img {
  order: 1;
  animation: imageFloat 3s ease-in-out infinite, imageFadeIn 1s ease-out;
}

@keyframes imageFadeIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes imageFloat {

  0%,
  100% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-15px);
  }
}

.hero-img img {
  border-radius: 50%;
  transition: transform 0.3s ease;
}

.hero-img:hover img {
  transform: scale(1.05) rotate(5deg);
}

.hero-text h1 {
  font-size: 52px;
  font-weight: 800;
  line-height: 62px;
  color: #FFD6A5;
  margin-bottom: 10px;
  text-shadow: 0 0 30px rgba(255, 214, 165, 0.3);
}

.highlight,
.text {
  background: linear-gradient(135deg, #3F1D38, #5a2d4f);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 900;
}

.highlight1 {
  background: linear-gradient(135deg, #AF5279, #d66a9a);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 900;
  animation: gradientPulse 3s ease-in-out infinite;
}

@keyframes gradientPulse {

  0%,
  100% {
    filter: brightness(1) saturate(1);
  }

  50% {
    filter: brightness(1.3) saturate(1.2);
  }
}

.role {
  margin-top: 15px;
  font-size: 36px;
  background: linear-gradient(135deg, #FFD6A5, #ffeac9, #FFD6A5);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
  animation: roleGradient 4s ease infinite;
}

@keyframes roleGradient {

  0%,
  100% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }
}

.intro-text {
  color: rgba(255, 214, 165, 0.95);
  margin-top: 18px;
  font-size: 17px;
  line-height: 28px;
  max-width: 500px;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  animation: textFadeIn 1s ease-out 0.4s both;
}

@keyframes textFadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Buttons */
.hero-buttons {
  margin-top: 30px;
  display: flex;
  gap: 18px;
  flex-wrap: wrap;
  justify-content: center;
  animation: buttonsFadeIn 1s ease-out 0.6s both;
}

@keyframes buttonsFadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.btn,
.btn-outline {
  padding: 14px 32px;
  font-size: 17px;
  font-weight: 600;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: none;
  position: relative;
  overflow: hidden;
}

.btn::before,
.btn-outline::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.btn:hover::before,
.btn-outline:hover::before {
  width: 300px;
  height: 300px;
}

.btn {
  margin-left: 0;
  background: linear-gradient(135deg, rgba(63, 29, 56, 0.9), rgba(175, 82, 121, 0.8));
  color: #FFD6A5;
  box-shadow: 0 4px 20px rgba(175, 82, 121, 0.4);
  border: 2px solid transparent;
}

.btn:hover {
  background: linear-gradient(135deg, rgba(175, 82, 121, 0.9), rgba(63, 29, 56, 0.8));
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 8px 30px rgba(175, 82, 121, 0.6);
  border-color: rgba(255, 214, 165, 0.3);
}

.btn-outline {
  background: rgba(63, 29, 56, 0.2);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(175, 82, 121, 0.6);
  color: #FFD6A5;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.btn-outline:hover {
  background: rgba(175, 82, 121, 0.3);
  border-color: #FFD6A5;
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 8px 25px rgba(255, 214, 165, 0.4);
  color: #fff;
}

.btn a,
.btn-outline a {
  text-decoration: none;
  color: inherit;
  position: relative;
  z-index: 1;
}

/* ------------------- HERO IMAGES --------------------- */
.hero-img,
.hero-img2 {
  width: 340px;
  height: 340px;
  overflow: hidden;
  border: 5px solid rgba(255, 214, 165, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 50px rgba(255, 214, 165, 0.3),
    0 0 100px rgba(175, 82, 121, 0.2);
  flex-shrink: 0;
  position: relative;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.hero-img::after,
.hero-img2::after {
  content: '';
  position: absolute;
  inset: -5px;
  border-radius: inherit;
  background: linear-gradient(135deg, #FFD6A5, #AF5279, #3F1D38);
  opacity: 0;
  z-index: -1;
  transition: opacity 0.4s ease;
}

.hero-img:hover::after,
.hero-img2:hover::after {
  opacity: 0.3;
  animation: borderGlow 2s ease-in-out infinite;
}

@keyframes borderGlow {

  0%,
  100% {
    filter: blur(10px);
  }

  50% {
    filter: blur(20px);
  }
}

.hero-img2 {
  border-radius: 40px;
  margin-left: 20px;
  width: auto;
}

.hero-img {
  border-radius: 50%;
}

.hero-img img,
.hero-img2 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease;
}

.hero-img:hover,
.hero-img2:hover {
  transform: scale(1.05);
  box-shadow: 0 0 60px rgba(255, 214, 165, 0.5),
    0 0 120px rgba(175, 82, 121, 0.3);
  border-color: rgba(175, 82, 121, 0.6);
}

/* ------------------- ABOUT / BIO SECTION --------------------- */
.about-us {
  padding: 90px 110px;
  background: linear-gradient(200deg, rgba(63, 29, 56, 0.7), rgba(175, 82, 121, 0.7), rgba(255, 214, 165, 0.5));
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  color: rgba(255, 214, 165, 0.98);
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  font-size: 17px;
  line-height: 30px;
  max-width: 1200px;
  margin: 60px auto;
  border-radius: 24px;
  text-align: center;
  border: 2px solid rgba(255, 214, 165, 0.25);
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(255, 214, 165, 0.15) inset;
  position: relative;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  animation: aboutFadeIn 1s ease-out;
}

@keyframes aboutFadeIn {
  from {
    opacity: 0;
    transform: translateY(40px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Shimmer effect removed */

.about-us:hover {
  box-shadow: 0 16px 56px rgba(175, 82, 121, 0.5),
    0 0 0 1px rgba(255, 214, 165, 0.3) inset;
  transform: translateY(-8px);
  border-color: rgba(175, 82, 121, 0.4);
}


.about-us h2 {
  font-size: 48px;
  font-weight: 900;
  margin-bottom: 30px;
  background: linear-gradient(135deg, #FFD6A5, #AF5279, #FFD6A5);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: none;
  position: relative;
  z-index: 1;
  animation: titleGradient 4s ease infinite;
}

@keyframes titleGradient {

  0%,
  100% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }
}

.about-us h2 u {
  text-decoration: none;
  position: relative;
}

.about-us h2 u::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 60%;
  height: 4px;
  background: linear-gradient(90deg, transparent, #AF5279, transparent);
  border-radius: 2px;
  animation: underlineGlow 2s ease-in-out infinite;
}

@keyframes underlineGlow {

  0%,
  100% {
    opacity: 0.6;
  }

  50% {
    opacity: 1;
    box-shadow: 0 0 10px #AF5279;
  }
}

.about-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
  margin-top: 40px;
  position: relative;
  z-index: 1;
}

/* Rotating Typing Text */
.rotating-text {
  font-size: 32px;
  color: rgba(255, 214, 165, 0.95);
  font-weight: 700;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.rotating-text .text {
  border-right: 3px solid rgba(255, 214, 165, 0.9);
  padding-right: 8px;
  white-space: nowrap;
  background: linear-gradient(135deg, #FFD6A5, #ffeac9);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.typing {
  animation: typing 1.5s steps(500), blink 3s infinite step-end alternate;
}

@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes blink {
  0% {
    border-color: #AF5279;
  }

  50% {
    border-color: #866a9c;
  }

  100% {
    border-color: #FFD6A5;
  }

}

/* ------------------- PROJECTS --------------------- */
.projects {
  margin: 50px auto;
  padding: 50px 40px;
  text-align: center;
  border: 2px solid rgba(175, 82, 121, 0.3);
  border-radius: 20px;
  background: rgba(63, 29, 56, 0.3);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  box-shadow: 0 8px 40px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(255, 214, 165, 0.1) inset;
  max-width: 90%;
  position: relative;
  overflow: hidden;
  transition: all 0.4s ease;
  animation: projectsFadeIn 1s ease-out;
}

.projects-bug {
  margin: 50px auto;
  padding: 50px 40px;
  text-align: center;
  border: 2px solid rgba(175, 82, 121, 0.3);
  border-radius: 20px;
  background: rgba(63, 29, 56, 0.3);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  box-shadow: 0 8px 40px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(255, 214, 165, 0.1) inset;
  max-width: 90%;
  position: relative;
  overflow: hidden;
  transition: all 0.4s ease;
  animation: projectsFadeIn 1s ease-out;
}

.projects-bug p {
  color: rgba(255, 214, 165, 0.95);
  margin-bottom: 20px;
  margin-top: 20px;
  font-family: monospace;
  font-size: 18px;
  font-style: oblique;
}
@keyframes projectsFadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.projects::before {
  content: '';
  position: absolute;
  inset: -2px;
  border-radius: 20px;
  padding: 2px;
  background: linear-gradient(135deg, #FFD6A5, #AF5279, #3F1D38);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0;
  transition: opacity 0.4s ease;
}

.projects:hover::before {
  opacity: 0.6;
  animation: borderRotate 3s linear infinite;
}

@keyframes borderRotate {
  0% {
    filter: hue-rotate(0deg);
  }

  100% {
    filter: hue-rotate(360deg);
  }
}

.projects:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 50px rgba(175, 82, 121, 0.5),
    0 0 0 1px rgba(255, 214, 165, 0.2) inset;
  border-color: rgba(175, 82, 121, 0.5);
}

.projects p {
  font-size: 19px;
  line-height: 34px;
  margin-bottom: 30px;
  color: rgba(255, 214, 165, 0.95);
  position: relative;
  z-index: 1;
}

.projects .btn {
  background: linear-gradient(135deg, rgba(63, 29, 56, 0.9), rgba(175, 82, 121, 0.8));
  padding: 14px 32px;
  border-radius: 12px;
  font-size: 18px;
  font-weight: 600;
  color: #FFD6A5;
  text-decoration: none;
  box-shadow: 0 6px 20px rgba(175, 82, 121, 0.4);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: inline-block;
  position: relative;
  z-index: 1;
  border: 2px solid transparent;
}

.projects .btn:hover {
  transform: translateY(-3px) scale(1.08);
  background: linear-gradient(135deg, rgba(175, 82, 121, 0.9), rgba(63, 29, 56, 0.8));
  box-shadow: 0 8px 30px rgba(175, 82, 121, 0.6);
  border-color: rgba(255, 214, 165, 0.3);
}

.line {
  border: none;
  height: 2px;
  background: linear-gradient(90deg, transparent, #AF5279, transparent);
  margin: 8px 0;
  animation: lineGlow 2s ease-in-out infinite;
}

@keyframes lineGlow {

  0%,
  100% {
    opacity: 0.6;
  }

  50% {
    opacity: 1;
    box-shadow: 0 0 10px #AF5279;
  }
}

/* ---------------------- PHOTO GALLERY ---------------------- */
.others {
  margin: 60px auto;
  width: 90%;
  max-width: 1100px;
  text-align: center;
}

.photo-title {
  font-size: 32px;
  margin-bottom: 25px;
  background: linear-gradient(135deg, #FFD6A5, #AF5279);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 800;
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}

.photo-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 60%;
  height: 3px;
  background: linear-gradient(90deg, transparent, #AF5279, transparent);
  border-radius: 2px;
}

.photo-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.photos {
  display: flex;
  gap: 25px;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 25px;
  border-radius: 16px;
  scroll-behavior: smooth;
  background: rgba(63, 29, 56, 0.25);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  width: 100%;
  scroll-snap-type: x mandatory;
  border: 1px solid rgba(255, 214, 165, 0.15);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.photos::-webkit-scrollbar {
  height: 10px;
}

.photos::-webkit-scrollbar-track {
  background: rgba(255, 214, 165, 0.1);
  border-radius: 10px;
}

.photos::-webkit-scrollbar-thumb {
  background: linear-gradient(90deg, #AF5279, #FFD6A5);
  border-radius: 10px;
  transition: background 0.3s ease;
}

.photos::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(90deg, #FFD6A5, #AF5279);
}

.photos img {
  min-width: 300px;
  max-width: 400px;
  scroll-snap-align: center;
  height: 400px;
  object-fit: cover;
  border-radius: 16px;
  border: 4px solid rgba(255, 214, 165, 0.3);
  box-shadow: 0 0 30px rgba(255, 214, 165, 0.2);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  flex-shrink: 0;
  position: relative;
}

.photos img:hover {
  transform: scale(1.08) translateY(-5px);
  border-color: rgba(175, 82, 121, 0.6);
  box-shadow: 0 0 50px rgba(175, 82, 121, 0.5),
    0 10px 30px rgba(0, 0, 0, 0.4);
}

/* Scroll Buttons */
.scroll-btn {
  position: absolute;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  border: 2px solid rgba(255, 214, 165, 0.3);
  cursor: pointer;
  background: rgba(63, 29, 56, 0.6);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  color: #FFD6A5;
  font-size: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 10;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.scroll-btn:hover {
  background: rgba(175, 82, 121, 0.7);
  transform: scale(1.15);
  border-color: rgba(255, 214, 165, 0.6);
  box-shadow: 0 0 25px rgba(175, 82, 121, 0.6),
    0 6px 20px rgba(0, 0, 0, 0.4);
}

.scroll-btn.left {
  left: -30px;
}

.scroll-btn.right {
  right: -30px;
}

/* ------------------ FOOTER ------------------ */
.footer {
  margin-top: 60px;
  padding: 40px 20px;
  background: rgba(63, 29, 56, 0.6);
  backdrop-filter: blur(10px);
  border-top: 2px solid rgba(255, 214, 165, 0.3);
  border-radius: 14px 14px 0 0;
  text-align: center;
  color: rgba(255, 214, 165, 0.9);
}

.footer-title {
  font-size: 60px;
  font-weight: 800;
  color: #AF5279;
  margin-bottom: 10px;
}

.footer-text {
  font-size: 16px;
  color: rgba(255, 214, 165, 0.85);
  max-width: 600px;
  margin: 0 auto 20px auto;
}

.footer-links {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.footer-link {
  color: rgba(255, 214, 165, 0.9);
  font-size: 17px;
  text-decoration: none;
  transition: 0.3s ease;
  border-bottom: 2px solid transparent;
}

.footer-link:hover {
  color: #FFD6A5;
  border-bottom: 2px solid #AF5279;
}


.Rules {
  margin-top: 12px;
  display: inline-flex;
  justify-content: center;
  gap: 8px;
  align-items: center;
  flex-wrap: wrap;
  background: rgba(175, 82, 121, 0.06);
  padding: 6px 10px;
  border-radius: 999px;
  border: 1px solid rgba(175, 82, 121, 0.08);
}

.Rules .footer-link {
  font-size: 14px;
  color: rgba(255, 214, 165, 0.95);
  padding: 6px 10px;
  border-bottom: none;
  transition: color 0.18s ease, transform 0.12s ease;
  display: inline-block;
}

.Rules .footer-link:hover {
  color: #FFF;
  transform: translateY(-2px);
  background: linear-gradient(90deg, rgba(175, 82, 121, 0.14), rgba(63, 29, 56, 0.06));
  border-radius: 8px;
  text-decoration: none;
}

.Rules .footer-link+.footer-link {
  margin-left: 6px;
}

@media (max-width: 480px) {
  .Rules {
    padding: 5px 8px;
  }

  .Rules .footer-link {
    font-size: 13px;
    padding: 5px 8px;
  }
}


.policy-page {
  padding: 56px 24px;
  max-width: 1100px;
  margin: 0 auto;
  color: rgba(0, 0, 0, 0.92);
}

.policy-page h1 {
  font-size: 56px;
  font-weight: 800;
  margin-bottom: 18px;
  color: rgba(0, 0, 0, 0.88);
}

.policy-subhead {
  margin-top: 36px;
  font-size: 22px;
  color: rgba(0, 0, 0, 0.75);
}

.policy-list {
  line-height: 2.0;
  margin-top: 14px;
  font-size: 20px;
  color: rgba(0, 0, 0, 0.85);
  padding-left: 22px;
}

@media (max-width: 900px) {
  .policy-page h1 {
    font-size: 44px;
  }

  .policy-list {
    font-size: 18px;
  }
}


.footer-bottom {
  font-size: 14px;
  margin-top: 10px;
  color: rgba(255, 214, 165, 0.7);
}

.footer-contact {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  background: rgba(63, 29, 56, 0.7);
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(63, 29, 56, 0.6);
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.footer-input,
.footer-textarea {
  width: 100%;
  padding: 12px 15px;
  border-radius: 10px;
  border: 2px solid #AF5279;
  background-color: rgba(63, 29, 56, 0.85);
  color: #FFD6A5;
  font-size: 16px;
  transition: border-color 0.3s ease;
  outline: none;
  resize: vertical;
}

.footer-input::placeholder,
.footer-textarea::placeholder {
  color: rgba(255, 214, 165, 0.7);
}

.footer-input:focus,
.footer-textarea:focus {
  border-color: #FFD6A5;
  background-color: rgba(63, 29, 56, 0.95);
  box-shadow: 0 0 8px #FFD6A5;
  color: #FFF;
}

.footer-btn {
  background-color: rgba(63, 29, 56, 0.85);
  color: #FFD6A5;
  padding: 12px 0;
  border: none;
  border-radius: 12px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
}

.footer-btn:hover {
  background-color: #af5279d9;
  transform: scale(1.05);
}


.code-block {
  font-family: 'Inter', 'Outfit', sans-serif;
  background: rgba(63, 29, 56, 0.5);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  color: rgba(255, 214, 165, 0.98);
  padding: 35px 30px;
  border-radius: 20px;
  line-height: 1.9;
  border: 2px solid rgba(255, 214, 165, 0.25);
  font-size: 17px;
  width: 100%;
  max-width: 650px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 214, 165, 0.1) inset;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  animation: codeBlockFadeIn 0.8s ease-out 0.3s both;
}

@keyframes codeBlockFadeIn {
  from {
    opacity: 0;
    transform: translateX(30px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Shimmer effect removed for cleaner look */

.code-block:hover {
  box-shadow: 0 12px 48px rgba(175, 82, 121, 0.4),
    0 0 0 1px rgba(255, 214, 165, 0.3) inset;
  transform: translateY(-5px);
  border-color: rgba(175, 82, 121, 0.4);
}

.code-block p {
  margin-bottom: 18px;
  position: relative;
  z-index: 1;
  padding-left: 8px;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}

.code-block p:last-child {
  margin-bottom: 0;
}

.code-block p:hover {
  transform: translateX(5px);
}


