/* ==================== SKILLS SECTION ==================== */

/* Main Skills Container */
.skills-section {
  padding: 80px 40px;
  background: linear-gradient(200deg, rgba(63, 29, 56, 0.7), rgba(175, 82, 121, 0.7), rgba(255, 214, 165, 0.5));
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border-radius: 24px;
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(255, 214, 165, 0.15) inset;
  border: 2px solid rgba(255, 214, 165, 0.25);
  margin: 60px 20px;
  position: relative;
  overflow: hidden;
  animation: sectionFadeIn 1s ease-out;
}

/* Entrance Animation */
@keyframes sectionFadeIn {
  from {
    opacity: 0;
    transform: translateY(50px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Shimmer effect removed for cleaner look */

/* Section Title */
.skills-section h2 {
  font-size: 48px;
  font-weight: 900;
  margin-bottom: 20px;
  text-align: center;
  background: linear-gradient(135deg, #FFD6A5, #AF5279, #FFD6A5);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  z-index: 1;
  animation: titleFloat 3s ease-in-out infinite, gradientShift 5s ease infinite;
}

@keyframes titleFloat {

  0%,
  100% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-8px);
  }
}

@keyframes gradientShift {

  0%,
  100% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }
}

/* Section Description */
.skills-section>p {
  font-size: 18px;
  line-height: 30px;
  color: rgba(255, 214, 165, 0.95);
  text-align: center;
  max-width: 800px;
  margin: 0 auto 50px auto;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  position: relative;
  z-index: 1;
}

/* Skills Grid */
.skills-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
  position: relative;
  z-index: 1;
}

/* Individual Skill Card */
.skill-item {
  padding: 35px 28px;
  background: rgba(63, 29, 56, 0.4);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 2px solid rgba(255, 214, 165, 0.2);
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 214, 165, 0.1) inset;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  opacity: 0;
  animation: cardSlideIn 0.6s ease-out forwards;
}

/* Staggered animation delays for each card */
.skill-item:nth-child(1) {
  animation-delay: 0.1s;
}

.skill-item:nth-child(2) {
  animation-delay: 0.2s;
}

.skill-item:nth-child(3) {
  animation-delay: 0.3s;
}

.skill-item:nth-child(4) {
  animation-delay: 0.4s;
}

.skill-item:nth-child(5) {
  animation-delay: 0.5s;
}

.skill-item:nth-child(6) {
  animation-delay: 0.6s;
}

.skill-item:nth-child(7) {
  animation-delay: 0.7s;
}

.skill-item:nth-child(8) {
  animation-delay: 0.8s;
}

@keyframes cardSlideIn {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
  }

  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Glowing border effect on hover */
.skill-item::before {
  content: '';
  position: absolute;
  inset: -2px;
  border-radius: 20px;
  background: linear-gradient(135deg, #FFD6A5, #AF5279, #3F1D38);
  opacity: 0;
  z-index: -1;
  transition: opacity 0.4s ease;
  filter: blur(8px);
}

.skill-item:hover::before {
  opacity: 0.6;
  animation: borderPulse 2s ease-in-out infinite;
}

@keyframes borderPulse {

  0%,
  100% {
    filter: blur(8px);
  }

  50% {
    filter: blur(15px);
  }
}

/* Card Hover Effect */
.skill-item:hover {
  transform: translateY(-10px) scale(1.02);
  border-color: rgba(175, 82, 121, 0.5);
  box-shadow: 0 16px 48px rgba(175, 82, 121, 0.4),
    0 0 0 1px rgba(255, 214, 165, 0.3) inset;
  background: rgba(63, 29, 56, 0.5);
}

/* Skill Icon */
.skill-item img {
  width: 70px;
  height: 70px;
  margin-bottom: 20px;
  filter: drop-shadow(0 0 15px rgba(255, 214, 165, 0.3));
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  animation: iconFloat 3s ease-in-out infinite;
}

@keyframes iconFloat {

  0%,
  100% {
    transform: translateY(0px) rotate(0deg);
  }

  50% {
    transform: translateY(-10px) rotate(5deg);
  }
}

.skill-item:hover img {
  transform: scale(1.15) rotate(360deg);
  filter: drop-shadow(0 0 25px rgba(175, 82, 121, 0.6));
}

/* Skill Title */
.skill-item h1 {
  font-size: 28px;
  font-weight: 800;
  margin-bottom: 15px;
  background: linear-gradient(135deg, #FFD6A5, #ffeac9);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.3s ease;
  position: relative;
}

.skill-item h1::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 3px;
  background: linear-gradient(90deg, #AF5279, #FFD6A5);
  border-radius: 2px;
  transition: width 0.3s ease;
}

.skill-item:hover h1::after {
  width: 60%;
}

.skill-item:hover h1 {
  filter: brightness(1.2);
  text-shadow: 0 0 20px rgba(255, 214, 165, 0.5);
}

/* Skill Description */
.skill-item p {
  font-size: 16px;
  line-height: 26px;
  color: rgba(255, 214, 165, 0.9);
  text-align: justify;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

/* Responsive Design */
@media (max-width: 768px) {
  .skills-section {
    padding: 50px 20px;
    margin: 40px 15px;
  }

  .skills-section h2 {
    font-size: 36px;
  }

  .skills-section>p {
    font-size: 16px;
    line-height: 26px;
  }

  .skills-list {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .skill-item {
    padding: 25px 20px;
  }

  .skill-item img {
    width: 60px;
    height: 60px;
  }

  .skill-item h1 {
    font-size: 24px;
  }

  .skill-item p {
    font-size: 15px;
    line-height: 24px;
  }
}

@media (max-width: 480px) {
  .skills-section {
    padding: 40px 15px;
    margin: 30px 10px;
  }

  .skills-section h2 {
    font-size: 32px;
  }

  .skill-item h1 {
    font-size: 22px;
  }
}