/* ==================== PROJECTS SECTION ==================== */

.projects-section {
  padding: 80px 40px;
  background: linear-gradient(200deg, rgba(63, 29, 56, 0.7), rgba(175, 82, 121, 0.7), rgba(255, 214, 165, 0.5));
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border-radius: 24px;
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(255, 214, 165, 0.15) inset;
  margin: 60px 20px;
  font-family: 'Inter', 'Outfit', sans-serif;
  text-align: center;
  color: #fff;
  border: 2px solid rgba(255, 214, 165, 0.25);
  position: relative;
  overflow: hidden;
  animation: sectionFadeIn 1s ease-out;
}

@keyframes sectionFadeIn {
  from {
    opacity: 0;
    transform: translateY(50px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.projects-section:hover {
  box-shadow: 0 16px 56px rgba(175, 82, 121, 0.5),
    0 0 0 1px rgba(255, 214, 165, 0.3) inset;
  transform: translateY(-5px);
  border-color: rgba(175, 82, 121, 0.4);
}

.projects-section h2 {
  font-size: 48px;
  font-weight: 900;
  margin-bottom: 20px;
  background: linear-gradient(135deg, #FFD6A5, #AF5279, #FFD6A5);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: titleGradient 4s ease infinite;
  position: relative;
}

.projects-section h2::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 60%;
  height: 4px;
  background: linear-gradient(90deg, transparent, #AF5279, transparent);
  border-radius: 2px;
}

@keyframes titleGradient {

  0%,
  100% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }
}

.projects-section>p {
  font-size: 18px;
  margin-bottom: 50px;
  color: rgba(255, 214, 165, 0.95);
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  line-height: 28px;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 35px;
  margin-bottom: 40px;
}

.project-item {
  background: rgba(63, 29, 56, 0.4);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  padding: 30px;
  border-radius: 20px;
  border: 2px solid rgba(255, 214, 165, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 214, 165, 0.1) inset;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  opacity: 0;
  animation: cardSlideIn 0.6s ease-out forwards;
}

.project-item:nth-child(1) {
  animation-delay: 0.1s;
}

.project-item:nth-child(2) {
  animation-delay: 0.2s;
}

.project-item:nth-child(3) {
  animation-delay: 0.3s;
}

.project-item:nth-child(4) {
  animation-delay: 0.4s;
}

@keyframes cardSlideIn {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
  }

  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.project-item::before {
  content: '';
  position: absolute;
  inset: -2px;
  border-radius: 20px;
  background: linear-gradient(135deg, #FFD6A5, #AF5279, #3F1D38);
  opacity: 0;
  z-index: -1;
  transition: opacity 0.4s ease;
  filter: blur(8px);
}

.project-item:hover::before {
  opacity: 0.6;
  animation: borderPulse 2s ease-in-out infinite;
}

@keyframes borderPulse {

  0%,
  100% {
    filter: blur(8px);
  }

  50% {
    filter: blur(15px);
  }
}

.project-item:hover {
  transform: translateY(-10px) scale(1.02);
  border-color: rgba(175, 82, 121, 0.5);
  box-shadow: 0 16px 48px rgba(175, 82, 121, 0.4),
    0 0 0 1px rgba(255, 214, 165, 0.3) inset;
}

.project-item img {
  border-radius: 16px;
  margin-bottom: 20px;
  width: 100%;
  height: 220px;
  object-fit: cover;
  border: 3px solid rgba(255, 214, 165, 0.2);
  transition: all 0.4s ease;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.project-item:hover img {
  transform: scale(1.05);
  border-color: rgba(175, 82, 121, 0.5);
  box-shadow: 0 8px 30px rgba(175, 82, 121, 0.4);
}

.project-item h3 {
  font-size: 26px;
  font-weight: 800;
  margin-bottom: 15px;
  background: linear-gradient(135deg, #FFD6A5, #ffeac9);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: none;
}

.project-item p {
  font-size: 16px;
  color: rgba(255, 214, 165, 0.9);
  margin-bottom: 20px;
  line-height: 26px;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

.project-item .btn {
  display: inline-block;
  padding: 12px 28px;
  border-radius: 12px;
  background: linear-gradient(135deg, rgba(63, 29, 56, 0.9), rgba(175, 82, 121, 0.8));
  border: 2px solid transparent;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 15px rgba(175, 82, 121, 0.3);
}

.project-item .btn a {
  color: #FFD6A5;
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
}

.project-item .btn:hover {
  background: linear-gradient(135deg, rgba(175, 82, 121, 0.9), rgba(63, 29, 56, 0.8));
  transform: translateY(-3px);
  box-shadow: 0 6px 25px rgba(175, 82, 121, 0.5);
  border-color: rgba(255, 214, 165, 0.3);
}

.thank-you {
  margin-top: 50px;
  margin-left: 50px;
  font-size: 32px;
  background: linear-gradient(135deg, #FFD6A5, #AF5279);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 900;
  text-shadow: none;
  animation: thankYouFloat 3s ease-in-out infinite;
}

@keyframes thankYouFloat {

  0%,
  100% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-10px);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .projects-section {
    padding: 50px 20px;
  }

  .projects-section h2 {
    font-size: 36px;
  }

  .projects-container {
    grid-template-columns: 1fr;
    gap: 25px;
  }

  .project-item {
    padding: 25px;
  }

  .project-item img {
    height: 200px;
  }

  .thank-you {
    font-size: 26px;
  }
}